	module.exports = {"pathContext":{"docHtml":"<h1>Git Webhook Configuration</h1>\n<h2>Overview</h2>\n<p>Argo CD polls Git repositories every three minutes to detect changes to the manifests. To eliminate\nthis delay from polling, the API server can be configured to receive webhook events. Argo CD supports\nGit webhook notifications from GitHub, GitLab, and BitBucket. The following explains how to configure\na Git webhook for GitHub, but the same process should be applicable to other providers.</p>\n<h3>1. Create The WebHook In The Git Provider</h3>\n<p>In your Git provider, navigate to the settings page where webhooks can be configured. The payload\nURL configured in the Git provider should use the <code>/api/webhook</code> endpoint of your Argo CD instance\n(e.g. [https://argocd.example.com/api/webhook]). If you wish to use a shared secret, input an\narbitrary value in the secret. This value will be used when configuring the webhook in the next step.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/webhook-config-1e3fed90ae9c19ddafe2b8b823f3114d-b7132.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 93.92338177014533%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Add Webhook\"\n        title=\"Add Webhook\"\n        src=\"/static/webhook-config-1e3fed90ae9c19ddafe2b8b823f3114d-fb8a0.png\"\n        srcset=\"/static/webhook-config-1e3fed90ae9c19ddafe2b8b823f3114d-1a291.png 148w,\n/static/webhook-config-1e3fed90ae9c19ddafe2b8b823f3114d-2bc4a.png 295w,\n/static/webhook-config-1e3fed90ae9c19ddafe2b8b823f3114d-fb8a0.png 590w,\n/static/webhook-config-1e3fed90ae9c19ddafe2b8b823f3114d-b7132.png 757w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>2. Configure Argo CD With The WebHook Secret Optional)</h3>\n<p>Configuring a webhook shared secret is optional, since Argo CD will still refresh applications\nrelated to the Git repository, even with unauthenticated webhook events. This is safe to do since\nthe contents of webhook payloads are considered untrusted, and will only result in a refresh of the\napplication (a process which already occurs at three-minute intervals). If Argo CD is publicly\naccessible, then configuring a webhook secret is recommended to prevent a DDoS attack.</p>\n<p>In the <code>argocd-secret</code> kubernetes secret, configure one of the following keys with the Git\nprovider's webhook secret configured in step 1.</p>\n<table>\n<thead>\n<tr>\n<th>Provider</th>\n<th>K8s Secret Key</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>GitHub</td>\n<td><code>github.webhook.secret</code></td>\n</tr>\n<tr>\n<td>GitLab</td>\n<td><code>gitlab.webhook.secret</code></td>\n</tr>\n<tr>\n<td>BitBucket</td>\n<td><code>bitbucket.webhook.uuid</code></td>\n</tr>\n</tbody>\n</table>\n<p>Edit the Argo CD kubernetes secret:</p>\n<pre><code class=\"language-bash\">kubectl edit secret argocd-secret -n argocd\n</code></pre>\n<p>TIP: for ease of entering secrets, kubernetes supports inputting secrets in the <code>stringData</code> field,\nwhich saves you the trouble of base64 encoding the values and copying it to the <code>data</code> field.\nSimply copy the shared webhook secret created in step 1, to the corresponding\nGitHub/GitLab/BitBucket key under the <code>stringData</code> field:</p>\n<pre><code class=\"language-yaml\">apiVersion: v1\nkind: Secret\nmetadata:\n  name: argocd-secret\n  namespace: argocd\ntype: Opaque\ndata:\n...\n\nstringData:\n  # github webhook secret\n  github.webhook.secret: shhhh! it's a github secret\n\n  # gitlab webhook secret\n  gitlab.webhook.secret: shhhh! it's a gitlab secret\n\n  # bitbucket webhook secret\n  bitbucket.webhook.uuid: your-bitbucket-uuid\n</code></pre>\n<p>After saving, the changes should take affect automatically.</p>","docPath":"argo-cd/docs/operator-manual/webhook","proj":"argo-cd"}}

